.header {
	height: 145px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	& .share-icon {
		width: 294px;
		height: 33px;
		& a {
			background-image: url(../../img/header__social-sprite.png);
			background-repeat: no-repeat;
			display: inline-block;
			width: 33px;
			height: 33px;
			margin: 0 28px 0 0; }
		& .share-icon__tel {
			background-position: left top; }
		& .share-icon__email {
			background-position: -65px; }
		& .share-icon__telegram {
			background-position: -65*2px; }
		& .share-icon__whatsapp {
			background-position: -65*3px; }
		& .share-icon__skype {
			background-position: right top;
			margin: 0; } } }
