html {
	scroll-behavior: smooth;
	overflow: hidden visible; }
html, body {
	min-width: 320px;
	max-width: 1920px;
	margin: 0;
	padding: 0;
	font-family: 'Montserrat'; }

*[class*="fix-container"] {
	width: 1240px;
	margin: 0 auto; }

*[class*="title"] {
	font-family: 'Playfair Display'; }

.montserat-medium-font {
	font-family: "Montserrat Medium";
	font-weight: 500; }
