.contacts {
	height: 400px;
	position: relative;
	& .contacts__fix-container {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		& .contacts__share-btn {
			margin: 95px 0 48px 0;
			width: 614px;
			& a {
				background-image: url(../../img/contacts__share-btn.png);
				background-repeat: no-repeat;
				display: inline-block;
				width: 96px;
				height: 96px;
				margin: 0 29px 0 0; }
			& .share-btn__tel {
				background-position-x: 0; }
			& .share-btn__email {
				background-position-x: -129px; }
			& .share-btn__telegram {
				background-position-x: -258px; }
			& .share-btn__whatsapp {
				background-position-x: -129-258px; }
			& .share-btn__skype {
				background-position-x: -258*2px;
				margin: 0; } }
		& .text-block {
			display: flex;
			flex-direction: column;
			align-items: center;
			& .text-block__title_montserrat {
				width: 269px;
				font-size: 24px;
				font-family: "Montserrat"; }
			& .text-block__subtitle_montserrat {
				width: 377px;
				font-size: 24px;
				font-family: "Montserrat";
				margin: 9px 0 0 0; }
			& .text-block__subtitle_medium {
				font-size: 24px;
				color: #3c3f8c;
				font-family: "Montserrat Medium";
				font-weight: 500; } }
		& .contacts__cloud {
			position: absolute;
			top: 28px;
			left: -246px; } } }
