.routine {
	height: 735px;
	overflow-x: hidden;
	overflow-y: visible;
	background-image: url(../../img/routine__bg.png);
	background-repeat: no-repeat;
	background-position-x: -15px;
	background-position-y: 3px;
	& .routine__fix-container {
		display: flex;
		justify-content: flex-start;
		& .routine__video-clip {
			margin: 186px 0 0 13px;
			& img {
				width: 561px;
				height: 317px; } }
		& .routine__text-block {
			width: 610px;
			margin: 115px 0 0 56px;
			& h1 {
				margin: 0 0 44px 0;
				font-size: 36px;
				font-weight: 400;
				text-transform: uppercase; }
			& ol {
				padding: 0 0 0 25px;
				margin: 0 0 0 0;
				line-height: 31.45px;
				list-style-type: none; }
			& li {
				font-size: 22px;
				font-weight: 400; } } } }
