.brief-services {
	height: 840px;
	position: relative;
	& .brief-services__cloud {
		position: absolute;
		top: 503px;
		left: -418px; }
	& .brief_services__pic {
		position: absolute;
		right: -57px;
		top: 2px; }
	& .brief-services__fix-container {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		& .brief-services__text-block {
			margin: 100px 0 0 0;
			width: 790px;
			height: 250px;
			& h1 {
				margin: 0;
				font-size: 44px;
				font-weight: 400;
				text-transform: uppercase; }
			& ul {
				padding: 0;
				list-style-type: none;
				margin: 61px 0 0 0; }
			& li {
				font-size: 24px;
				font-weight: 400;
				&:before {
					content: "-";
					margin: 0 7px 0 0; } }
			& .services-item2 {
				margin: 5px 0 0 0; } }
		& form {
			margin: 184px 0 0 0;
			width: 610px;
			height: 47px;
			* {
				font-family: 'Montserrat'; }
			& input {
				padding: 0 0 0 13px;
				width: 175px;
				height: 44px;
				border-radius: 10px;
				border: 1px solid #282d87;
				color: rgba(40, 45, 135, 0.5);
				font-size: 20px;
				margin: 0 15px 0 0; }
			& button {
				width: 190px;
				height: 46px;
				border-radius: 10px;
				background-color: #ff9a23;
				font-size: 20px;
				color: #ffffff;
				border: 1px solid #ff9a23;
				text-align: center;
				font-weight: 400; } } } }
