.footer {
	height: 300px;
	& .footer__fix-container {
		height: 196px;
		display: flex;
		align-items: flex-start;
		justify-content: center;
		& .footer__text-block {
			margin: 58px 0 0 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			& .text-block__item-1 {
				color: #000000;
				font-size: 16px; }
			& .text-block__item-2 {
				margin: 5px 0 0 0;
				color: #000000;
				font-size: 16px; }
			& .text-block__link {
				text-decoration: none;
				color: #3c3f8c;
				font-size: 16px;
				font-family: "Montserrat Medium"; } } }
	& .footer__logo-container {
		height: 105px;
		background-color: #7577f9;
		display: flex;
		align-items: center;
		justify-content: center; } }
